
import API from '@/API'
import XButton from 'vux/src/components/x-button/index.vue'

import echarts from 'echarts'

export default {
components:{
XButton
},
data(){
return{
paper_name: '',
items: [],
answer: {
name:'',
question1:{
first: []
}
},
result: '',
ageRange: '0 ~ 3',
scores:[],
commonScores:[]
}
},
created(){
this.getReport( this.$route.params.id )
this.getAnswer( this.$route.params.id )
},
updated(){
const myChart = echarts.init(this.$refs.myChart);
myChart.setOption({
tooltip : {
trigger: 'axis',
axisPointer : {            // 
type : 'shadow'        
}
},
legend: {
data:['',''],
textStyle : {
fontSize : 14
}
},
grid: {
left: '3%',
right: '4%',
bottom: '3%',
containLabel: true
},
xAxis : [
{
type : 'category',
data : ["", "", ""],
axisLabel : {
fontSize : 14
}
}
],
yAxis : [
{
type : 'value'
}
],
series : [
{
name:'',
type:'bar',
data:this.commonScores,
itemStyle:{
normal:{
color: 'rgba(1,152,101,.8)'
}
},
},
{
name:'',
type:'bar',
data:this.scores,
itemStyle:{
normal:{
color: 'rgba(0,173,239,.8)'
}
},
}
]
});
},
methods:{
async getReport(id){
let res = await API.getReport(id)
console.log(res.data)
console.log(res.data.data)
this.paper_name = res.data.paper_name

let scores = res.data.scores
let commonScores = res.data.commonScores.map( (item) =>{
return +item
})
this.scores = [scores[0]+scores[1]+scores[5], scores[2], scores[3]]
this.commonScores = [commonScores[0]+commonScores[1]+commonScores[5], commonScores[2], commonScores[3]]
console.log(this.scores)
console.log(this.commonScores)

this.items = res.data.data.filter( (item) =>{
return item.title.indexOf('') == -1 && item.title.indexOf('') == -1
})

let data = ''
for(let item of this.items){
data += item.title
}
if( data.indexOf('') ){
this.result = ''
}
},
async getAnswer(id){
let res = await API.getAnswer(id)
console.log(res)

const age = +res.data.age
if( age > 12 ){
this.ageRange = '12'
}else if( age>6 && age <=12 ){
this.ageRange = '6 ~ 12'
}else if( age>3 && age <=6 ){
this.ageRange = '3 ~ 6'
}else{
this.ageRange = '0 ~ 3'
}
if( res.data.question1.first[0] == '' ){
res.data.question1.first[0] = ''
}
this.answer = res.data
}
}
}



